* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.status {
  margin-bottom: 30px;
  font-size: 30px;
}

.board {
  background: linear-gradient(-45deg, green, #302b5f);
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.board-row {
  display: flex;
  justify-content: center;
}

.board-row button {
  outline: none;
  font-size: 40px;
  width: 100px;
  height: 100px;
  margin: 5px;
}

.reset {
  height: 40px;
}

.reset button {
  margin-top: 30px;
  font-size: 20px;
  background-color: chartreuse;
  border-radius: 10px;
  padding: 4px 10px;
}

@media screen and (max-width: 356px) {
  .board-row button {
    width: 60px;
    height: 60px;
  }
}