:root {
    --w: 580px;
}

* {
    margin: 0;padding: 0;
}

.Calc-App {
    height: 100vh;
    background: linear-gradient(135deg, #091130, #9995cf);
    display: flex;
    align-items: center;
    justify-content: center;
}

.Calc-Container {
    border-radius: 10px;
    width: var(--w);
    min-height: 200px;
    background-color: #2f3142;
    box-shadow: 0 0 20px #c5b6b6;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Keys {
    min-height: 100px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 5px 30px;
}

.Key {
    width: calc(var(--w)/5 - 40px);
    height: calc(var(--w)/5 - 40px);
    border-radius: 50%;
    margin: 5px;
    border: 1px solid #d6e9d7;
    background-color: #1e1f2b;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 900 !important;
    font-size: 35px;
    color: #d6e9d7;
    cursor: pointer;
    clip-path: circle(51%);
    order: 0;
    user-select: none;
    transition: font-size .12s;
}

.Key:hover,
.Key:active {
    background-color: #3e404e;
    
}

.Key:active {font-size: 20px;}

.key-color-c {
    color: #be2a10;
    font-family: Arial;
}

.key-color-f {
    color: yellowgreen;
}

.bg-equal {
    background-color: #86680d;
}

.bg-equal:hover,
.bg-equal:active {
    background-color: #c59913;
}

.second-order-keys {
    padding-bottom: 10px;
    margin: 5px 80px;
    border-bottom: 1px solid #555055;
    display: flex;
    justify-content: space-between;
}

.calc-input {
    width: 100%;
    padding: 0 5px;
    margin: 0 10px;
    font-size: 30px;
    text-align: right;
    background-color: #0000001f;
    caret-color: yellowgreen;
    color: white;
    outline: none;
    border: none;
}

.calc-adv,
.calc-backspace {
    background-color: #1e1f2b !important;
}

.calc-backspace {
    color: #86680d !important;
}

.calc-adv {
    color: #fff !important;
}

.calc-resoult {
    height: 80px;
    font-size: 30px;
    margin: 5px 80px;
    text-align: right;
    color: #807a7a;
}

@media screen and (max-aspect-ratio: 85/136) {

    @media screen and (max-width: 680px) {
        :root {
            --w: 460px;
        }

        .Calc-Container {
            margin: 10px;
            height: calc(100vh - 60px);
        }

        .second-order-keys {
            margin: 5px;
        }

        .calc-resoult {
            margin: 5px 40px;
        }

        .Key {
            width: calc(var(--w)/4 - 35px);
            height: calc(var(--w)/4 - 35px);
            font-size: 30px;
        }

        .Keys .Key:nth-child(6) {order: 1}
        .Keys .Key:nth-child(7) {order: 2}
        .Keys .Key:nth-child(8) {order: 3}
        .Keys .Key:nth-child(5) {order: 4}
        .Keys .Key:nth-child(11) {order: 4}
        .Keys .Key:nth-child(12) {order: 5}
        .Keys .Key:nth-child(13) {order: 6}
        .Keys .Key:nth-child(9) {order: 7}
        .Keys .Key:nth-child(16) {order: 8}
        .Keys .Key:nth-child(17) {order: 9}
        .Keys .Key:nth-child(18) {order: 10}
        .Keys .Key:nth-child(10) {order: 11}
        .Keys .Key:nth-child(15) {order: 12}
        .Keys .Key:nth-child(19) {order: 13}
        .Keys .Key:nth-child(14) {order: 14}
        .Keys .Key:nth-child(20) {order: 15}
    }

    @media screen and (max-width: 487px) {
        .Key {
            width: 75px;
            height: 75px;
            font-size: 25px;
        }
    }

    @media screen and (max-width: 468px) {
        .Key {
            width: 72px;
            height: 72px;
            font-size: 25px;
        }
    }

    @media screen and (max-width: 455px) {
        .Key {
            width: 72px;
            height: 72px;
            margin: 3px;
        }

        .Keys {
            padding: 5px 10px;
        }

        .second-order-keys {
            flex-wrap: wrap;
        }

        .calc-input {
            order: -1;
            margin: 10px 0;
        }

        .calc-resoult {
            margin: 5px;
        }
    }

    @media screen and (max-width: 400px) {
        .Keys {
            padding: 2px;
        }

        .Key {
            width: 70px;
            height: 70px;
            font-size: 20px;
        }

        .Calc-Container {
            padding: 20px 10px;
        }
    }

    @media screen and (max-width: 368px) {
        .Key {
            width: 66px;
            height: 66px;
        }
    }

    @media screen and (max-width: 345px) {
        .Key {
            width: 60px;
            height: 60px;
        }

        .calc-input {
            font-size: 26px;
        }
    }

    @media screen and (max-width: 315px) {
        .Key {
            width: calc(100vw/4 - 19px);
            height: calc(100vw/4 - 19px);
        }
    }
}